* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat';
}

body {
    line-height: 1.4;
}

a {
    color: #333;
    text-decoration: none;
}

.container {
    /* padding: 0 1rem; */
}

.btn {
    display: inline-block;
    border: none;
    background: #555;
    color: #fff;
    padding: 7px 20px;
    cursor: pointer;
    transition: all .2s ease-in-out;
}

.btn:hover {
    background: #666;
}